.table {
  table-layout: fixed;
  width: 100%;

  border: 1px solid gray;
  border-radius: 10px;
  margin-top: 40px;
  margin-bottom: 40px;
  margin-right: auto;
  margin-left: auto;
  box-shadow: 5px 5px 19px -1px #000000;
}

.thead__th {
  font-size: 20px;
  font-weight: 500px;
  border: 1px solid gray;
  background-color: greenyellow;
}

.thead__th:nth-child(1) {
  width: 15%;
}

.thead__th:nth-child(2) {
  width: 25%;
}

.thead__th:nth-child(3) {
  width: 15%;
}

.thead__th:nth-child(4) {
  width: 35%;
}

th,
td {
  padding: 10px;
  font-size: 20px;
  font-weight: 500px;
  border: 1px solid gray;
  font-family: 'Rock Salt', cursive;
}

thead th,
tfoot th {
  font-family: 'Rock Salt', cursive;
}

th {
  letter-spacing: 2px;
}

td {
  letter-spacing: 1px;
}

tbody td {
  text-align: center;
}

tfoot th {
  text-align: left;
}

tbody tr:nth-child(odd) {
  background-color: rgba(154, 216, 201, 0.842); /* фон нечетных строк */
}

tbody tr:nth-child(even) {
  background-color: #ffffff; /* фон четных строк */
}
